<template>
<div>
   <div id="head-top">
          <img src="../../../static/img/fangdajing.png" id="search">
          <div id="top">的任何的关系更搞笑的感谢分享</div>
         <router-link to="/daohang">
              <img  id="toux"     src="../../../static/img/touxiang.png" >
         </router-link>    
  </div>
  <!-- 轮播图模块 -->
   <div id="content1">  
    <swiper :options="swiperOption"  v-if="foodList1.length">
       
    <!-- slides -->
    <swiper-slide>
        <ul class="list">
          <li :key="index" v-for="(value,index) in foodList1" @click="$router.push({name:'food',params:{title:value.title}})">
              <img :src="'https://fuss10.elemecdn.com/'+value.image_url">
              <p>{{value.title}}</p>
          </li>
      </ul>
      </swiper-slide>
    <swiper-slide>
          <ul class="list">
          <li :key="index" v-for="(value,index) in foodList2"  @click="$router.push({name:'food',params:{title:value.title}})">
              <img :src="'https://fuss10.elemecdn.com/'+value.image_url">
              <p>{{value.title}}</p>
          </li>
      </ul>
      </swiper-slide>
      
     
      <!-- </swiper-slide>  -->
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
    
  </swiper>
   </div>
  <div id="content3">
      
  </div>


</div>
 

</template>
<script>
export default {
  name: "wai-first",
  created() {
    this.$route.params.pId = 1;
    this.$store.state.page = 0;
    // console.log("a");
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        loop: true,
        initialSlide: 0
      },
      foodList1: [],
      foodList2: []
    };
  },
  // computed: {
  //   swiper() {
  //     return this.$refs.mySwiper.swiper
  //   }
  // },
  mounted() {
    this.getlist();

  },
  methods: {
    // let _this = this;
    getlist() {
      const url = "https://elm.cangdu.org/v2/index_entry";
      this.$http({
        method: "get",
        url: url
      }).then(res => {
        for (var i = 0; i < res.data.length / 2; i++) {
          this.$set(this.foodList1, i, res.data[i]);
          this.$set(this.foodList2, i, res.data[i + 8]);
        //   this.$set(this.swiperOption, 'loop', true);
       

          //   this.foodList1[i] = res.data[i] ;
          //   this.foodList2[i] = res.data[i+8];
        //   _this.swiperOption.
        }
         
      });
    }
  }
};
</script>
<style scoped>
#head-top {
  width: 100%;
  height: 0.45rem;
  background-color: #3190e8;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  font-size: 0.16rem;
  color: #fff;
}
#head-logo {
  left: 0.1rem;
  font-weight: 400;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#top {
  width: 50%;
  position: absolute;
  left: 25%;
  height: 0.35rem;
  top: 30%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
#search {
  height: 0.35rem;
  position: absolute;
  left: 0.13rem;
  top: 50%;
  transform: translateY(-50%);
}
#toux {
  height: 0.35rem;
  position: absolute;
  right: 0.13rem;
  top: 50%;
  transform: translateY(-50%);
}
#content1 {
  padding-top: 0.49rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  border-bottom: 0.006rem solid #e4e4e4;
  height: 2.47rem;
}
swiper-slide {
  height: 2.47rem;
}
#content2 {
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  border-bottom: 0.006rem solid #e4e4e4;
  height: 2.47rem;
  border: 1px solid rebeccapurple;
}
.list {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: center;
}
.list > li {
  width: 25%;
  height: 50%;
  text-align: center;
  padding-bottom: 0.37rem;
}
.list > li > img {
  margin-bottom: 0.17rem;
  width: 0.43rem;
  height: 0.43rem;
}
.list > li > p {
  text-align: center;
  font-size: 0.14rem;
  color: #666;
}
</style>



