<template>
    <div>
      <div id="head-top">
          <img @click="back()" id="head-back" src="../../../static/img/back.png" >
          <span id="top">{{this.$route.params.title}}</span>
       </div>
       <!-- 标题下三个功能 -->
       <div class="sort">
           <div class="sort-item">
             <span>{{this.$route.params.title}}</span>
             <img @click="sh = !sh" v-if="sh" src="../../../static/img/up1.png">
             <img  @click="sh = !sh" v-else src="../../../static/img/up2.png" >
           </div>
           <div class="sort-item">
               <span>排序</span>
                <img @click="sh1 = !sh1" v-if=sh1 src="../../../static/img/up1.png">
             <img  @click="sh1 = !sh1" v-else src="../../../static/img/up2.png" >
           </div>
           <div class="sort-item">
               <span>筛选</span>
              <img @click="sh2 = !sh2" v-if="sh2" src="../../../static/img/up1.png">
             <img  @click="sh2 = !sh2" v-else src="../../../static/img/up2.png" >
           </div>
       </div>
       <!-- 分类列表 -->
      <div class="sort-type">
          <!-- 左边 -->
      <div class="type-left">
          <ul class="left-ul">
              <li>
               <span class="s1">异国料理</span>
               <span class="count">1002</span>
              </li>
          </ul>
         
           <ul class="left-ull">
              <li :class="inde == index ? 'active':''" @click="reveal(index)" :key="index" v-for="(item,index) in typeList ">
                
                <img class="imgfood"  src="https://fuss10.elemecdn.com/b/ff/533cf9617bd57fe1dfb05603bebcfpng.png" >
               <span class="s11">{{item.name}}</span>
               <span class="count1">{{item.count}}</span>
               <img class="rc" src="../../../static/img/right.png" alt="">
              </li>
          </ul>
      </div>
          <!-- 右边 -->
      <div class="type-right" v-if="inde">
          <ul>
               <!-- <li :key="index" v-for="(item,index) in typeList[inde].sub_categories">
                 <span class="kinds">{{item.name}}</span>
                 <span class="shu">{{item.count}}</span>
              </li> -->
               <li  :key="index" v-for="(item,index) in typeList">

                 <span class="kinds">{{item.name}}</span>
                 <span class="shu">{{item.count}}</span>

              </li>
          </ul>
      </div>
      </div>
    
    </div>

</template>
<script>
export default {
    name:'food',
    data(){
     return{
        sh:true,
        sh1:true,
        sh2:true,
        typeList:[],
        typeList2:[],
        inde:"",
        active:'w'
     }
    },
    created() {
      this.getType();
        console.log(this.$route.params);
    //    this.inde = 


    },
    methods:{
        getType() {
        const url = "https://elm.cangdu.org/shopping/v2/restaurant/category";
        this.$http({
          method: 'get',
          url: url
        }).then((res) => {
           console.log(res.data);
           this.typeList = res.data;
           console.log( this.typeList[2].image_url);
        });
        
      },
      reveal(a){
          
          console.log(a);
          
          this.inde = a;
          console.log(this.inde);
      },
      back(){
          this.$router.back();
      }
    }

}
</script>
<style scoped>
.active{
    background-color: #fff;
}
#head-top{
     width: 100%;
     height:0.45rem;
     background-color:#3190e8;
     position: fixed;
     left: 0;
     top: 0;
     z-index: 100;
     font-size: 0.16rem;
     color: #fff;
 }
 #head-back{
     left: 0.1rem;
     height: 0.18rem;
     position: absolute;
     top: 50%;
     transform: translateY(-50%);
 }

 #top{
    text-align: center;
    font-weight: 700;
     position: absolute;
     right: 30%;
     top: 50%;
     transform: translate(-50%,-50%); 
     font-size: 0.19rem;
 }
 .sort{
    padding-top: 0.07rem;
    background-color: #fff;
    border-bottom: .006rem solid #f1f1f1;
    position: fixed;
    top: .45rem;
    right: 0; 
    width: 100%;
    display: flex;
    justify-content: space-around;
    z-index: 13;
    box-sizing: border-box;
 }
 .sort-item{
    height: .24rem;
    border-right: .006rem solid #e4e4e4;
    margin: .09rem;
 }
 .sort-item span{
       color: #333;
       font-size: .15rem;
       margin-left: .10rem;
 }
 .sort-item img{
     height:.28rem;
     vertical-align: middle;
    transition: all .3s;
    margin-right: .15rem;
    transform: rotate(180deg);
    transition: all .3s;
   
 }
 .sort-item:last-of-type{
     border: none;
 }
 .sort-type{
    width: 100%;
    position: absolute;
    display: flex;
    top: 0.95rem;
    left: 0;
    border-top: .006rem solid #e4e4e4;
    background-color: #fff;
    height: 4.26rem;
 }
 .type-left{
    flex: 1;
    background-color: #f1f1f1;
    /* height: 4.7rem; */
    overflow-y: auto;
 }
 .left-ul{
     display: none;
 }
 .left-ul li{
    display: flex;
    justify-content: space-between;
    padding: 0 .1.9rem;
    padding-left: 0.1rem;
    border: 1px solid gold;
 }
 .left-ull li{
    height: 0.47rem;
    padding: 0 .1.9rem;
    padding-left: 0.1rem;

 }
 .s1{
    font-size: .13rem;
    color: #666;
    margin-top: .18rem;
    margin-bottom: .18rem;

 }
 .s11{
    font-size: .13rem;
    color: #666;
    margin-top: .18rem;
    margin-bottom: .18rem;
    margin-right: .32rem;
    margin-left: .1rem;
 
 }
 .count{
    background-color: #ccc;
    font-size: .1rem;
    color: #fff;
    padding: 0 .03rem;
    border: .006rem solid #ccc;
    border-radius: .19rem;
    vertical-align: middle;
    margin-right: .1rem;
    margin-top: .18rem;
    margin-bottom: .18rem;
 }
 .count1{
    display: inline-block;
    background-color: #ccc;
    font-size: .1rem;
    color: #fff;
    padding: 0 .03rem;
    border: .006rem solid #ccc;
    border-radius: .19rem;
    vertical-align: middle;
    margin-top: .18rem;
    margin-bottom: .18rem;
 }
 .rc{
     float: right;
     margin-top: .2rem;
     margin-right: .08rem;
     height: 0.08rem;
     width: 0.08rem;
 }
 .imgfood{
     width: .19rem;
     height: .19rem;
     float: left;
     margin-top: .15rem;
 }
 .type-right{
     flex: 1;
    background-color:#fff;
    /* height: 4.7rem; */
    overflow-y: auto;
    margin-left: .05rem;
 }
 .type-right li{
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    height: .47rem;
    line-height: .47rem;
    padding-right: .1rem;
    border-bottom: .006rem solid #e4e4e4;
 }
 .type-right li>span{
     color: #666;
     font-size: .13rem
 }
 
</style>





